"use client";

import React, { useMemo, useState } from "react";
import { Modal } from "@/components/ui/modal";
import {
    Table,
    TableHeader,
    TableBody,
    TableRow,
    TableCell,
} from "@/components/ui/table";
import Badge from "@/components/ui/badge/Badge";

/* -------------------- TYPES -------------------- */

type TicketStatus =
    | "Draft"
    | "Submitted"
    | "In Progress"
    | "On Hold"
    | "Resolved"
    | "Rejected";

type Priority = "Low" | "Medium" | "High" | "Critical";

const STATUS_OPTIONS: TicketStatus[] = [
    "Draft",
    "Submitted",
    "In Progress",
    "On Hold",
    "Resolved",
    "Rejected",
];

const PRIORITIES: Priority[] = ["Low", "Medium", "High", "Critical"];

const GROUPS = {
    IT: ["John", "Alice"],
    HR: ["Priya"],
    Finance: ["David"],
};

type AssignmentGroup = keyof typeof GROUPS;

const CI_ASSETS = ["Laptop", "VPN", "Email", "Payroll"];
const SOURCES = ["Web", "Chat", "Integration", "Email"] as const;

const CATEGORIES = {
    IT: ["Hardware", "Software", "Access"],
    HR: ["Payroll", "Leave"],
    Finance: ["Reimbursement", "Invoice"],
};

interface Attachment {
    name: string;
    url: string;
}

interface TimelineEntry {
    status: TicketStatus;
    user: string;
    timestamp: string;
}

interface Ticket {
    id: number;
    summary: string;
    description: string;
    status: TicketStatus;
    priority: Priority;
    category: keyof typeof CATEGORIES;
    subcategory: string;
    assignmentGroup: AssignmentGroup;
    assignedTo: string;
    caller: string;
    ciAsset: string;
    source: typeof SOURCES[number];
    createdAt: string;
    attachments: Attachment[];
    timeline: TimelineEntry[];
}

/* -------------------- PAGE -------------------- */

export default function HelpDeskPage() {
    const [tickets, setTickets] = useState<Ticket[]>([]);
    const [selected, setSelected] = useState<Ticket | null>(null);
    const [open, setOpen] = useState(false);
    const [filters, setFilters] = useState({ status: "", ci: "", month: "" });
    const [page, setPage] = useState(1);
    const PAGE_SIZE = 5;

    const filtered = useMemo(() => {
        return tickets.filter((t) => {
            const d = new Date(t.createdAt);
            return (
                (!filters.status || t.status === filters.status) &&
                (!filters.ci || t.ciAsset === filters.ci) &&
                (!filters.month || d.getMonth() + 1 === Number(filters.month))
            );
        });
    }, [tickets, filters]);

    const paginated = filtered.slice(
        (page - 1) * PAGE_SIZE,
        page * PAGE_SIZE
    );

    return (
        <div className="p-6 space-y-6">
            {/* Header */}
            <div className="flex justify-between items-center">
                <h1 className="text-xl font-semibold">Help Desk</h1>
                <button
                    className="bg-brand-600 text-white px-4 py-2 rounded"
                    onClick={() => {
                        setSelected(null);
                        setOpen(true);
                    }}
                >
                    New Ticket
                </button>
            </div>

            {/* Filters */}
            <div className="flex gap-4">
                <select
                    className="border px-3 py-2 rounded"
                    value={filters.status}
                    onChange={(e) => setFilters({ ...filters, status: e.target.value })}
                >
                    <option value="">All Status</option>
                    {STATUS_OPTIONS.map((s) => (
                        <option key={s}>{s}</option>
                    ))}
                </select>

                <select
                    className="border px-3 py-2 rounded"
                    value={filters.ci}
                    onChange={(e) => setFilters({ ...filters, ci: e.target.value })}
                >
                    <option value="">All CI</option>
                    {CI_ASSETS.map((c) => (
                        <option key={c}>{c}</option>
                    ))}
                </select>

                <select
                    className="border px-3 py-2 rounded"
                    value={filters.month}
                    onChange={(e) => setFilters({ ...filters, month: e.target.value })}
                >
                    <option value="">Month</option>
                    {[...Array(12)].map((_, i) => (
                        <option key={i} value={i + 1}>
                            {new Date(2025, i).toLocaleString("default", { month: "long" })}
                        </option>
                    ))}
                </select>
            </div>

            {/* Table */}
            <div className="rounded-xl border bg-white overflow-hidden">
                <Table>
                    <TableHeader>
                        <TableRow>
                            <TableCell isHeader>ID</TableCell>
                            <TableCell isHeader>Summary</TableCell>
                            <TableCell isHeader>Status</TableCell>
                            <TableCell isHeader>Priority</TableCell>
                            <TableCell isHeader>Group</TableCell>
                            <TableCell isHeader>CI</TableCell>
                        </TableRow>
                    </TableHeader>

                    <TableBody>
                        {paginated.map((t) => (
                            <TableRow key={t.id} className="hover:bg-gray-50">
                                <TableCell>{t.id}</TableCell>
                                <TableCell>
                                    <span
                                        className="text-blue-600 cursor-pointer font-medium hover:underline"
                                        onClick={() => {
                                            setSelected(t);
                                            setOpen(true);
                                        }}
                                    >
                                        {t.summary}
                                    </span>
                                </TableCell>
                                <TableCell>
                                    <Badge
                                        size="sm"
                                        color={
                                            t.status === "Resolved"
                                                ? "success"
                                                : t.status === "Rejected"
                                                    ? "error"
                                                    : t.status === "On Hold"
                                                        ? "info"
                                                        : t.status === "Submitted"
                                                            ? "warning"
                                                            : "secondary"
                                        }
                                    >
                                        {t.status}
                                    </Badge>
                                </TableCell>
                                <TableCell>{t.priority}</TableCell>
                                <TableCell>{t.assignmentGroup}</TableCell>
                                <TableCell>{t.ciAsset}</TableCell>
                            </TableRow>
                        ))}
                    </TableBody>
                </Table>
            </div>

            {/* Pagination */}
            <div className="flex justify-center gap-2">
                {Array.from({ length: Math.ceil(filtered.length / PAGE_SIZE) }).map(
                    (_, i) => (
                        <button
                            key={i}
                            className={`px-3 py-1 rounded border ${page === i + 1 ? "bg-brand-600 text-white" : ""
                                }`}
                            onClick={() => setPage(i + 1)}
                        >
                            {i + 1}
                        </button>
                    )
                )}
            </div>

            {/* Modal */}
            <Modal isOpen={open} onClose={() => setOpen(false)} className="max-w-3xl">
                <TicketForm
                    ticket={selected}
                    onSave={(t) => {
                        setTickets((prev) =>
                            prev.some((p) => p.id === t.id)
                                ? prev.map((p) => (p.id === t.id ? t : p))
                                : [...prev, t]
                        );
                        setOpen(false);
                    }}
                />
            </Modal>
        </div>
    );
}

/* -------------------- FORM -------------------- */

function TicketForm({
    ticket,
    onSave,
}: {
    ticket: Ticket | null;
    onSave: (t: Ticket) => void;
}) {
    const isNew = !ticket;

    const [form, setForm] = useState<Ticket>(
        ticket ?? {
            id: Date.now(),
            summary: "",
            description: "",
            status: "Draft",
            priority: "Medium",
            category: "IT",
            subcategory: "",
            assignmentGroup: "IT",
            assignedTo: "",
            caller: "Current User",
            ciAsset: "",
            source: "Web",
            createdAt: new Date().toISOString(),
            attachments: [],
            timeline: [],
        }
    );

    const users = GROUPS[form.assignmentGroup];
    const subcats = CATEGORIES[form.category];

    const updateStatus = (s: TicketStatus) => {
        if (s !== form.status) {
            setForm({
                ...form,
                status: s,
                timeline: [
                    ...form.timeline,
                    { status: s, user: "Current User", timestamp: new Date().toISOString() },
                ],
            });
        }
    };

    return (
        <div className="space-y-4">
            <h2 className="text-lg font-semibold">
                {isNew ? "New Ticket" : `Ticket #${form.id}`}
            </h2>

            <input
                className="w-full border px-3 py-2 rounded"
                placeholder="Summary"
                value={form.summary}
                onChange={(e) => setForm({ ...form, summary: e.target.value })}
            />

            <textarea
                className="w-full border px-3 py-2 rounded"
                placeholder="Description"
                value={form.description}
                onChange={(e) => setForm({ ...form, description: e.target.value })}
            />

            <div className="grid grid-cols-2 gap-3">
                <select
                    className="border px-3 py-2 rounded"
                    value={form.status}
                    onChange={(e) => updateStatus(e.target.value as TicketStatus)}
                >
                    {STATUS_OPTIONS.map((s) => (
                        <option key={s}>{s}</option>
                    ))}
                </select>

                <select
                    className="border px-3 py-2 rounded"
                    value={form.priority}
                    onChange={(e) =>
                        setForm({ ...form, priority: e.target.value as Priority })
                    }
                >
                    {PRIORITIES.map((p) => (
                        <option key={p}>{p}</option>
                    ))}
                </select>
            </div>

            <div className="border-l pl-4 space-y-3">
                {form.timeline.map((t, i) => (
                    <div key={i}>
                        <div className="font-medium">{t.status}</div>
                        <div className="text-xs text-gray-500">
                            {t.user} Â· {new Date(t.timestamp).toLocaleString()}
                        </div>
                    </div>
                ))}
            </div>

            <div className="flex justify-end">
                <button
                    className="bg-brand-600 text-white px-4 py-2 rounded"
                    onClick={() => onSave(form)}
                >
                    Save
                </button>
            </div>
        </div>
    );
}
